.overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: .5s ease;
    background-color: white;
}
  
.text {
    color: black;
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
}

.name {
    font-size: 1.5rem;
    font-weight: 400;
}

.overlay-hr {
    color: black;
}

.technology {
    font-size: 1rem;
}

.details {
    padding: .5rem;
    color: black;
    font-weight: 400;
}

.details > a {
    color: black;
}

.details > a:hover {
    font-weight: 600;
}

